@import url("https://fonts.googleapis.com/css?family=Roboto");
html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*, *:before, *:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

body {
  font-family: 'Roboto', sans-serif;
  font-size: 16px;
  margin: 0; }

.app {
  padding: 1rem; }

.user-profile-popup {
  position: fixed;
  top: 100px;
  left: 100px;
  right: 100px;
  bottom: 100px;
  background: #FFF;
  padding: 1rem; }

.user-profile-popup-blocker {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.8); }



